import { AnimatePresence } from 'framer-motion';
import { lazy, Suspense } from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import ScrollToTop from './components/ScrollToTop';
import AppFooter from './components/shared/AppFooter';
import AppHeader from './components/shared/AppHeader';
import './css/App.css';
import UseScrollToTop from './hooks/useScrollToTop';
import { createHashHistory } from 'history'; // Import hashHistory
const About = lazy(() => import('./pages/AboutMe'));
const Contact = lazy(() => import('./pages/Contact.jsx'));
const Home = lazy(() => import('./pages/Home'));
const Projects = lazy(() => import('./pages/Projects'));
const ProjectSingle = lazy(() => import('./pages/ProjectSingle.jsx'));

const hashHistory = createHashHistory(); // Create an instance of hashHistory
function App() {
	return (
		<AnimatePresence>
			<div className=" bg-secondary-light dark:bg-primary-dark transition duration-300">
				<Router history={hashHistory}>
					<ScrollToTop />
					<AppHeader />
					<Suspense fallback={""}>
						<Routes>
							<Route path="/" element={<Home />} />
							<Route path="projects" element={<Projects />} />
							
							<Route
								path="projects/single-project-domotique"
								element={<ProjectSingle context="domotique" />}
							/>							
							
							<Route
								path="projects/single-project-aquarium"
								element={<ProjectSingle context="aquarium" />}
							/>

							<Route
								path="projects/single-project-serveur"
								element={<ProjectSingle context="serveur" />}
							/>

							<Route
								path="projects/single-project-solaires"
								element={<ProjectSingle context="solaires" />}
							/>

							<Route path="about" element={<About />} />
						</Routes>
					</Suspense>
					<AppFooter />
				</Router>
				<UseScrollToTop />
			</div>
		</AnimatePresence>
	);
}

export default App;
